.square{
  background-color: aqua;
  color: black;
  font-size: 300%;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  padding-top: 48px;
  padding-bottom: 48px;
  padding-right: 48px;
  padding-left: 48px;
  border-radius: 18px;
  margin-top: 2px;
  margin-bottom: 2px;
  margin-left: 2px;
  margin-right: 2px;
  height: 4px;
  width: 4px;
}

.board-row {
  background-color: black;
}

.status {
  background-color: black;
  color: white;
  font-size: 222%;
  font-family: Arial, Helvetica, sans-serif;
}

.game {
  background-color: pink;
}

.game-info {
  background-color: blueviolet;
  color: white;
  font-family:Arial, Helvetica, sans-serif;
  font-weight: bolder;
}